<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('StatusCode') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <section>
                            <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT"
                                :LegendArray="[]" :RenderRowActionMethod="renderActions"
                                    v-on:ActionButtonClick="actionButtonClick" :widgets="[]" IdentityColumn="id"
                                    :HeaderText="$t('ServiceAppointment')" :listType="['List']" :ListData="StatusCodemodalList"
                                    :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                                    :ListDataCallBackFunction="FetchRecords" :showCheckBox="true" :SortExp="SortExp"
                                    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                                    v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                                    v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">                                
                                </tg-list>
                                  <AddEditStatusCodeModal v-if="AddEditStatusCode" :Data="StatusCodeData"></AddEditStatusCodeModal>
                            </section>
                        </div>
                        <div class="d-flex justify-content-end mr-3 mb-3">
                            <a title="Save" v-on:click="SaveStatusCodeMapping()" class="btn btn-success mr-2 mt-3"><i class="fa fa-floppy-o pr-2" aria-hidden="true"></i>{{ $t("Save") }}</a>
                            <a title="Close" v-on:click="Close()" class="btn btn-primary delete-btn mt-3"><i class="fa fa-times pr-2" aria-hidden="true"></i>{{ $t("Close") }} </a>
                        </div>
                    </div>
                </div>
            </div>
    </div>
</template>
<script>
import Schema from "@/views/WorkType/StatusCodeSchema";
import AddEditStatusCodeModal from '../../components/Modal/AddEditStatusCodeModal.vue'
import DataServices from "@/services/DataService";
export default {
    components: { AddEditStatusCodeModal },
    data() {
        return {
            AddEditStatusCode: false,
            noRecords: false,
            TotalRecords: 0,
            listActions: Schema.listActions,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            Headers: [{
                "COLUMN_NAME": "status_code",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO Status Code",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "description",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Description",
                "DISPLAY_ORDER": 2
            }           
            ],
            SubHeaders: [{
                "COLUMN_NAME": "status_code",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO Sub Status Code",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "description",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Description",
                "DISPLAY_ORDER": 2
            }           
            ],
            listheaderbuttons: Schema.buttons,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            leftSearchFields: null,
            StatusCodemodalList: [],
            StatusCodeData:[],
            SubStatusCodemodalList:[]
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchRecords();
    },
    methods: {
        FetchRecords: async function (item, event) {            
            var vueObj = this;
            var srchCondition = "";
            var url = `worktypeid=${this.$route.params.id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                url = url + "&search=" + srchCondition;
            }
            await DataServices.GetFsmStatusCodeList(url).then(
                function (response) {                    
                    vueObj.StatusCodemodalList = [];
                    if (response.data != "" && response.data != null) {
                        if (response.data.result.data[0].TOTALRECORDS > 0) {
                            vueObj.StatusCodemodalList = response.data.result.data;
                            vueObj.StatusCodemodalList.forEach((x) => {
                                x.child = {
                                    onExpandChild: vueObj.onExpandChild,
                                    isShowChild: false,
                                    isShowExpandButton: true,
                                    data: []                                    
                                }
                            });
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.StatusCodemodalList = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.SAData = [];
                        vueObj.TotalRecords = 0
                    }
                     setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                });
        },
        onExpandChild:async function (item) { 
            var vueObj=this;
            var url = `worktypeid=${this.$route.params.id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&statuscodeid=${item.id}`;
            await DataServices.GetFsmStatusCodeList(url).then(
                function (response) {
                   // debugger                    
                    if (response.data != "" && response.data != null) {
                        //debugger
                        vueObj.Headers=vueObj.SubHeaders;
                       // debugger
                        if (response.data.result.data[0].TOTALRECORDS > 0) {
                            item.child.data = response.data.result.data;   
                            $.each(item.child.data, function (index, item) {
                                var forms = "";
                                var arr = item.selectedForm;
                                $.each(arr, function (i, itm) {                                    
                                    forms += (forms == "") ? itm.name : ", " + itm.name;
                                });                                
                                item.selectedForm = forms;
                                });                                                     
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;                            
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            item.child.data = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        item.child.data = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                });
        },
        Close: function () {
            this.$parent.AddWOStatusCode = false;
        },
        DeleteStatusCode: function (id, isdeleteable,isCheckChild) {
            var vm = this;
            var val = "";
            if(isCheckChild){
            if (isdeleteable == 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticketing Service Board")), function () {
                    var postJSON = {
                        ids: id,
                        moduleName: "FSM",
                        subModuleCode: "",
                        refCode: "TALYGEN_FSM_STATUS_CODE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityDataFSM(objectPostString).then(response => {
                        if (response.data.status == "Success") {
                            vm.FetchRecords();
                            setTimeout(function () {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('StatusCode')]), "Success", true, vm.$t('Alert'));
                            }, 500);
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('StatusCode')]), "failure", true, vm.$t('Alert'));
                        }

                    });
                });
            }}else{
                vm.commonDeleteData();
            }
        },
        actionButtonClick(callbackfunction, item, index, event,isChild) {                   
            switch (callbackfunction) {
                case 'Delete':     
                    this.DeleteStatusCode(item.id,item.Isdeleteable,isChild);
                    break;
                case 'Edit':                    
                    this.openPopupForAddEditStatusCode(item,isChild);
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':                        
                        action.isVisible=true;
                        action.isDisabled = row.Isdeleteable>0?true:false;
                        break;
                    case 'Delete':
                        action.isVisible=true;
                        action.isDisabled = row.Isdeleteable>0?true:false;
                        break;
                    default: {
                        action.isDisabled = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        openPopupForAddEditStatusCode: function (row,isCheckChild) {      
           // debugger    
            this.StatusCodeData=row;
            this.AddEditStatusCode = !this.AddEditStatusCode;
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "CommonAddData":
                    this.openPopupForAddEditStatusCode(null);
                    break;
            }
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticketing Service Board")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "ticketing",
                        refCode: "TALYGEN_FSM_STATUS_CODE_WORKTYPE_MAPPING"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchRecords();
                            setTimeout(function () {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('StatusCodeMapping')]), "Success", true, vm.$t('Alert'));
                            }, 500);
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('StatusCodeMapping')]), "failure", true, vm.$t('Alert'));
                        }

                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        SaveStatusCodeMapping() {
            var vm = this;
            var data = [];
            vm.StatusCodemodalList.forEach(item => {
                if (item.isSelected == true) {
                    var statusCodeId = item.id;
                    var dataObject = {
                        statuscode_id: statusCodeId,
                        worktype_id: this.$route.params.id
                    };
                    data.push(dataObject);
                }
                if (item.isSelected == true) {
                    item.child.data.forEach(itm => {
                        if (itm.isSelected == true) {
                            var statusChildCodeId = itm.id;
                            var dataObjectChild = {
                                statuscode_id: statusChildCodeId,
                                worktype_id: this.$route.params.id
                            };
                            data.push(dataObjectChild);
                        }
                    });
                }
            });
            if (data.length > 0) {
                var params = JSON.stringify(data);
                DataServices.SaveWorktypeStatusCodeMapping(params).then((response) => {
                    if (response.data.status == 'Success') {
                        if (vm.statusCodeId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("StatusCode")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("StatusCode")]), "success", true, vm.$t("Alert"));
                        }
                        vm.Close();
                        vm.$parent.FetchRecords();
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("StatusCode")]), "failure", true, vm.$t("Alert"));
                        vm.Close();
                    }
                });
            } else {
                vm.ShowAlert(vm.$t("PleaseSelectAnyRecordsForFurther"), "warning", true, vm.$t("Alert"));
            }
        },
        BacktoWorktypeList: function () {
            this.$router.push({ name: 'WorkTypeListing' });
        }
    },
}
</script>