<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('CDCCode') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddCDCCode')"></div>
                                </div>
                            </div>
                        </span>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: this.$t('SelectCdcCode'),
                            name: "CDCCode",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectProductName'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        this.GetDdlRecords_CDCCODE();
    },
    methods: {
        GetDdlRecords_CDCCODE: async function () {
            var vm = this;
            var url = `id=&&moduleName=${'FSM'}&type=${'FSM_CDCCODE'}&search=&refId=${this.$route.params.id}`;
            await DataServices.GetDDLData(url).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        Close: function () {
            this.$parent.AddCDCCodeMapping = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var cdcvalue = '';
            var i = 0;
            for (i = 0; i < formfields.CDCCode.length; i++) {
                if (i == 0) {
                    cdcvalue = formfields.CDCCode[i].value;
                } else {
                    cdcvalue = cdcvalue + "," + formfields.CDCCode[i].value;
                }
            }
            var jsonobj = {
                id: this.$route.params.id,
                CdcCode: cdcvalue,
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveCdcCode(params).then((response) => {

                if (response.data.status == 'Success') {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("StatusCode")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$parent.FetchRecords();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>