<template>
    <section>
        <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
            :RenderRowActionMethod="() => { }" :widgets="[]" IdentityColumn="MappingId" :HeaderText="$t('ServiceAppointment')"
            :listType="['List']" :ListData="SKillData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords"  :ListDataCallBackFunction="FetchRecords"
            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'StatusId'">
                <listing-status :isfor="'WORKTYPE_SKILL_MAPPING'" v-if="isCRMShowStausDrop(data.row.StatusId, 'Ticketing')"
                    ref="updatestatus" v-bind:dataid="data.row.MappingId" data-controller="Crm"
                    v-bind:statuslist="StatusListForListing" v-bind:item="data.StatusId" data-action="index"
                    v-bind:dataselected="data.row.StatusId == 1001 ? 'Active' : 'Inactive'" v-bind:callbackfunction="FetchRecords"
                    v-bind:isselectedvaluestring="true">
                </listing-status>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
                    <span v-bind:class="getStatusClass( data.row.status_id, 'status_id')">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                </a>
            </template>
        </template>
        </tg-list>
        <AddSkillMappingModal v-if="AddSkillMapping" :MappingId="MappingId"></AddSkillMappingModal>
    </section>
</template>
<script>
import Schema from "@/views/crew/CrewResourceSchema";
import AddSkillMappingModal from '../../../components/Modal/AddSkillMappingModal.vue'
import DataServices from "@/services/DataService";
export default {
    components:{AddSkillMappingModal},
    data() {
        return {
            AddSkillMapping:false,
            MappingId:0,
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            Headers: [{
                "COLUMN_NAME": "SkillName",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Skill Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "WorkName",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Work Type Name",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "StatusId",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 3
            },
            ],
            listheaderbuttons:  Schema.buttons,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            user_id: null,
            companyId: null,
            leftSearchFields: null,
            SKillData: [],
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
        }
    },
    created: async function () {
        var vm = this;
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        await vm.FetchRecords();
    },
    methods: {
        FetchRecords:async function () {
            var vueObj = this;
            var url = `WtId=${this.$route.params.id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            await DataServices.FsmGetSkillMappingByWorkType(url).then(
                function (response) {
                    vueObj.TotalRecords = 0;
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "StatusId") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vueObj.SKillData = [];
                    if (response.data != "" && response.data != null) {
                            if (response.data.status == "Success") {
                                if (response.data.result.data.length > 0) {
                                    vueObj.SKillData = response.data.result.data;
                                    vueObj.TotalRecords = vueObj.SKillData[0].TOTALRECORDS;
                                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                                    vueObj.CurrentPage = vueObj.PageNumber;
                                    vueObj.listheaderbuttons[1].isdisabled=true;
                                    vueObj.noRecord = false;
                                }
                            }
                            else {
                                vueObj.noRecord = true;
                                vueObj.SKillData = [];
                                vueObj.TotalRecords = 0
                            }
                        }
                        else {
                            vueObj.noRecord = true;
                            vueObj.SKillData = [];
                            vueObj.Headers = [];
                            vueObj.TotalRecords = 0
                        }                  
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                });
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        openPopupForAddSkillmapping: function (id) {
                if(id == undefined)
                    id = 0;
                this.MappingId = id;
                this.AddSkillMapping = !this.AddSkillMapping;
            },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "CommonAddData":
                    this.openPopupForAddSkillmapping(null);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "BackToList":
                    this.BacktoWorktypeList();
                    break;
            }
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticketing Service Board")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "ticketing",
                        refCode: "TALYGEN_FSM_worktype_skill_mapping"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        if (response.data[0].Status == "Success") {
                            vm.listheaderbuttons[1].isdisabled=true;
                            vm.FetchRecords();
                            setTimeout(function () {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('SkillMapping')]), "Success", true, vm.$t('Alert'));
                        }, 500);
                        }
                        else {
                             vm.ShowAlert(vm.$t('DeletedError', [vm.$t('SkillMapping')]), "failure", true, vm.$t('Alert'));
                        }
                    
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 bg-success text-center text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        BacktoWorktypeList: function(){
            this.$router.push({ name: 'WorkTypeListing' });
        }
    },
}
</script>