<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
            :RenderRowActionMethod="() => { }" :widgets="[]" IdentityColumn="id" :HeaderText="$t('ServiceAppointment')"
            :listType="['List']" :ListData="StatusCodeList" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchRecords" :showCheckBox="true" :SortExp="SortExp"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" 
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">            
        </tg-list>
        <AddWOStatusCodeModal v-if="AddWOStatusCode" :MappingId="MappingId"></AddWOStatusCodeModal>
        <SetFormMappingModal v-if="SetFormMapping" :Form_Id="workTypeId" :Form="moduleName" :IsCheckLocate="CompanyType"></SetFormMappingModal>
    </section>
</template>
<script>
import Schema from "@/views/WorkType/WOStatusMappingSchema";
import AddWOStatusCodeModal from '../../../components/Modal/AddWOStatusCodeModal.vue'
import SetFormMappingModal from '../../../components/Modal/SetFormMappingModal.vue'
import DataServices from "@/services/DataService";
export default {
    props:{
        CompanyType:{
            type: String,
            Required: true
        }
    },
    components: { AddWOStatusCodeModal, SetFormMappingModal },
    data() {
        return {            
            moduleName:'WorkType',
            AddWOStatusCode: false,
            SetFormMapping: false,
            FormMappingId: 0,
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            Headers: [{
                "COLUMN_NAME": "status_code",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO Status Code",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "description",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Description",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "selectedForm",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Selected Form",
                "DISPLAY_ORDER": 3
            },
            ],
            SubHeaders: [{
                "COLUMN_NAME": "status_code",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO Sub Status Code",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "description",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Description",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "selectedForm",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Selected Form",
                "DISPLAY_ORDER": 3                
            },
            ],
            listheaderbuttons: Schema.buttons,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            leftSearchFields: null,
            StatusCodeList: [],
            workTypeId:null,
            isLoading:false
        }
    },
    created: async function () {        
        var vm = this;
        vm.workTypeId = this.$route.params.id
        await vm.FetchRecords();
    },
    methods: {
        leftCommonSearch(){},
        FetchRecords: async function (item, event) {
            var vueObj = this;
            var srchCondition = "";
            var url = `worktypeid=${this.$route.params.id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                url = url + "&search=" + srchCondition;
            }
            await DataServices.GetFsmWorktypeStatusCodeMappingList(url).then(
                function (response) {
                    vueObj.StatusCodeList = [];
                    if (response.data != "" && response.data != null) {
                        if (response.data.result.data[0].TOTALRECORDS > 0) {
                            vueObj.StatusCodeList = response.data.result.data;
                            vueObj.StatusCodeList.forEach((x) => {
                                x.child = {
                                    onExpandChild: vueObj.onExpandChild,
                                    isShowChild: false,
                                    isShowExpandButton: true,
                                    data: []
                                }
                            });  
                            $.each(vueObj.StatusCodeList, function (index, item) {
                                var forms = "";
                                var arr = item.selectedForm;
                                $.each(arr, function (i, itm) {                                    
                                    forms += (forms == "") ? itm.name : ", " + itm.name;
                                });                                
                                item.selectedForm = forms;
                            });                          
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.listheaderbuttons[2].isdisabled=true;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.StatusCodeList = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.SAData = [];
                        vueObj.TotalRecords = 0
                    }                  
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                });
        },
        onExpandChild: function (item) {
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `worktypeid=${vueObj.workTypeId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&parentstatuscodeid=${item.id}`;
            DataServices.GetFsmWorktypeStatusCodeMappingList(url)
                .then(function (response) {
                    if (response.data != "" && response.data != null) {
                        if (response.data.status == "Success") {
                            if (response.data.result.data.length > 0) {                                 
                                vueObj.Headers = vueObj.SubHeaders;
                                item.child.data = response.data.result.data;
                                $.each(item.child.data, function (index, item) {
                                var forms = "";
                                var arr = item.selectedForm;
                                $.each(arr, function (i, itm) {                                    
                                    forms += (forms == "") ? itm.name : ", " + itm.name;
                                });                                
                                item.selectedForm = forms;
                                });
                                vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                                vueObj.CurrentPage = vueObj.PageNumber;
                                vueObj.listheaderbuttons[2].isdisabled=true;
                                vueObj.noRecord = false;
                                vueObj.isLoading = false;
                            }
                            else {
                                vueObj.noRecord = true;
                                item.child.data = [];
                                vueObj.TotalRecords = 0;
                                vueObj.isLoading = false;
                            }
                        }
                        else {
                            vueObj.noRecord = true;
                            item.child.data = [];
                            vueObj.TotalRecords = 0;
                            vueObj.isLoading = false;
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        item.child.data = [];
                        vueObj.TotalRecords = 0;
                        vueObj.isLoading = false;
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        openPopupForAddWOStatusCode: function (id) {
            if (id == undefined)
                id = 0;
            this.MappingId = id;
            this.AddWOStatusCode = !this.AddWOStatusCode;
        },
        openPopupForSetFormMapping: function (id) {
            this.SetFormMapping = !this.SetFormMapping;
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "OpenSetFormMapping":
                    this.openPopupForSetFormMapping(null);
                    break;
                case "openaddWorkType":
                    this.openPopupForAddWOStatusCode(null);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "BackToList":
                    this.BacktoWorktypeList();
                    break;
            }
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticketing Service Board")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "ticketing",
                        refCode: "TALYGEN_FSM_STATUS_CODE_WORKTYPE_MAPPING"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        if (response.data[0].Status == "Success") {
                            vm.listheaderbuttons[2].isdisabled=true;
                            vm.FetchRecords();
                            setTimeout(function () {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('StatusCodeMapping')]), "Success", true, vm.$t('Alert'));
                            }, 500);
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('StatusCodeMapping')]), "failure", true, vm.$t('Alert'));
                        }

                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        BacktoWorktypeList: function () {
            this.$router.push({ name: 'WorkTypeListing' });
        }
    },
}
</script>