<template>
    <section>
        <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
            :RenderRowActionMethod="() => { }" :widgets="[]" IdentityColumn="id"
            :HeaderText="$t('ServiceAppointment')" :listType="['List']" :ListData="CdcList" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :ListDataCallBackFunction="FetchRecords" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <a class="active">
                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                            v-if="data.row.STATUS_NAME == 'Active'">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                            v-if="data.row.STATUS_NAME == 'Inactive'">
                            {{ $t('Inactive') }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ISAH'">
                    <td><span>{{(data.row.ISAH == '1'?'Yes': 'No')}}</span></td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ISWIRECENTER'">
                    <td><span>{{(data.row.ISWIRECENTER == '1'?'Yes': 'No')}}</span></td>
                </template>
            </template>
        </tg-list>
        <AddCDCCodeMappingModal v-if="AddCDCCodeMapping"></AddCDCCodeMappingModal>
    </section>
</template>
<script>
import Schema from "@/views/WorkType/CDCMapSchema";
import AddCDCCodeMappingModal from '../../../components/Modal/AddCDCCodeMappingModal.vue'
import DataServices from "@/services/DataService";
export default {
    components: { AddCDCCodeMappingModal },
    data() {
        return {
            AddCDCCodeMapping: false,
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            Headers: [{
                "COLUMN_NAME": "CDC_CODE",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "CDC COde",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "SBA_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "SBA Id",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "STATE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "State",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "ABBREVIATIONS",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Abbrevations",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "ISAH",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Is After Hour",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "ISWIRECENTER",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Is Wire Center",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "DIVISION_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Invoice Grouping",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 3
            }
            ],
            listheaderbuttons: Schema.buttons,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            user_id: null,
            companyId: null,
            leftSearchFields: null,
            CdcList: [],
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
        }
    },
    created: async function () {
        var vm = this;
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        await vm.FetchRecords();
    },
    methods: {
        FetchRecords: async function () {
            var vueObj = this;
            var url = `work_type_id=${this.$route.params.id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            await DataServices.GetFsmCdcList(url).then(
                function (response) {
                    vueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == 'STATUS_NAME') {
                        column.DISPLAY_NAME = 'Status'
                        column.settings = {
                            isInSlot: true,
                            isForTag: true
                        }
                    }
                    if (column.COLUMN_NAME == 'ISAH') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'ISWIRECENTER') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                });
                    vueObj.CdcList = [];
                    vueObj.TotalRecords = 0;
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            vueObj.CdcList = response.data.result.data;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.listheaderbuttons[1].isdisabled=true;
                            if (vueObj.CdcList.length > 0) {
                                vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                                vueObj.NoRecord = false;
                            }
                            else {
                                vueObj.NoRecord = true;
                            }
                        }
                        else {
                            vueObj.NoRecord = true;
                        }                       
                    }                  
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                });
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        openPopupForAddCDCCodemapping: function (id) {
            if (id == undefined)
                id = 0;
            this.AddCDCCodeMapping = !this.AddCDCCodeMapping;
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "CommonAddData":
                    this.openPopupForAddCDCCodemapping(null);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "BackToList":
                    this.BacktoWorktypeList();
                    break;
            }
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticketing Service Board")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "ticketing",
                        refCode: "TALYGEN_FSM_CDC_CODE_WORKTYPE_MAPPING"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        if (response.data[0].Status == "Success") {
                            vm.listheaderbuttons[1].isdisabled=true;
                            vm.FetchRecords();
                            setTimeout(function () {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('CDCCodeMapping')]), "Success", true, vm.$t('Alert'));
                            }, 500);
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('CDCCodeMapping')]), "failure", true, vm.$t('Alert'));
                        }

                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },  
        BacktoWorktypeList: function () {
            this.$router.push({ name: 'WorkTypeListing' });
        }
    },
}
</script>