<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('StatusCode') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        Data: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "TextField",
                            label: this.$t('StatusCode'),
                            name: "StatusCode",
                            value: "",
                            placeholder: this.$t('EnterStatusCodeName'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ParentStatusCode'),
                            name: "ParentStatusCode",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            statusCodeId:0
        }
    },
    created: async function () {
        var vueObj =this;
        vueObj.GetDdlRecords_PARENT_STATUSCODE();
        if(vueObj.Data !=null){
            vueObj.statusCodeId= vueObj.Data.id;
            (vueObj.FormSchema)[0].Data[0].value=vueObj.Data.status_code;
            (vueObj.FormSchema)[0].Data[1].value=vueObj.Data.parent_id==null?0:vueObj.Data.parent_id;
            (vueObj.FormSchema)[1].Data[0].value=vueObj.Data.description;
        }
    },
    methods: {
        GetDdlRecords_PARENT_STATUSCODE: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'FSM_WO_STATUSCODE'}&search=&refId=`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[1].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        Close: function () {
            this.$parent.AddEditStatusCode = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                id: vm.statusCodeId,
                StatusCode: formfields.StatusCode,
                ParentStatusCode: formfields.ParentStatusCode,
                Description: formfields.Description
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveStatusCode(params).then((response) => {
                if (response.data.status == 'Success') {
                        if (vm.statusCodeId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("StatusCode")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("StatusCode")]), "success", true, vm.$t("Alert"));
                        }
                        vm.Close();
                        vm.$parent.FetchRecords();
                    }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("StatusCode")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>