<template>
    <div id="divMainFormData" v-cloak>
        <section id="main-content" class="main-content">           
            <div class="left-menu-tab div-click-event hr_jobpost_a">
                <div class="col-md-12 p-0">
                    <div class="theme-primary partition" v-for="item in WorktypeSourceList">
                        <span class="p-name text-white textelipsis">{{ item.worktype_name }}</span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 1 }"
                            data-bs-target="#tab-detail-content" href="javascript:void(0)" data-bs-toggle="tab" role="tab"
                            id="tab-Details" v-on:click="loadData('detail');">
                            <em class="fa fa-file-text" aria-hidden="true" aria-selected="true"></em>
                            {{ $t('Details') }}
                        </a>
                    </li>
                    <li class="nav-item" v-if="(user_type == 'NU' && created_by == user_id) || (user_type == 'CA' || user_type == 'PM')">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 2 }"
                            data-bs-target="#tab-service-content" href="javascript:void(0)" id="tab-service-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('Service');">
                            <em class="fa fa-calendar" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('ServiceAppointment') }}
                        </a>
                    </li>
                    <li class="nav-item" v-if="CompanyType=='locating'">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 3 }"
                            data-bs-target="#tab-WOStatus-content" href="javascript:void(0)" id="tab-WOStatus-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('WoStatus');">
                            <em class="fa fa-bar-chart" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('WOStatusMapping') }}
                        </a>
                    </li>
                    <li class="nav-item" v-if="(user_type == 'NU' && created_by == user_id) || (user_type == 'CA' || user_type == 'PM')">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 4 }"
                            data-bs-target="#tab-Skill-content" href="javascript:void(0)" id="tab-Skill-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('Skill');">
                            <em class="fa fa-bar-chart" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('SkillMapping') }}
                        </a>
                    </li>
                    <li class="nav-item" v-if="(user_type == 'NU' && created_by == user_id) || (user_type == 'CA' || user_type == 'PM')">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 5 }"
                            data-bs-target="#tab-Product-content" href="javascript:void(0)" id="tab-Product-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('Product');">
                            <em class="fa fa-bar-chart" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('ProductMapping') }}
                        </a>
                    </li>
                    <li class="nav-item" v-if="CompanyType=='locating'">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 6 }"
                            data-bs-target="#tab-CDCCode-content" href="javascript:void(0)" id="tab-CDCCode-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('CDCCode');">
                            <em class="fa fa-code" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('CDCCodeMapping') }}
                        </a>
                    </li>
                    <li class="nav-item" v-if="CompanyType=='nonlocating'">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 7 }"
                            data-bs-target="#tab-FormMapping-content" href="javascript:void(0)" id="tab-FormMapping-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('FormMapping');">
                            <em class="fa fa-bar-chart" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('FormMapping') }}
                        </a>
                    </li>
                </ul>
            </div>
            <div class="right-content">
                <div class="col-md-12 p-0 mb-2" v-if="isDetail || isService">
                    <div class="theme-primary partition p-actions-expand">
                        <span class="p-name text-white" id="span_ForSelectedTabName">{{ tabNameTitle }}</span>                      
                        <span class="p-actions float-right">
                            <RouterLink to="/Ticketing/worktypelisting" class="p-action-btn text-white float-right">
                                <em class="fa fa-angle-double-left"></em>
                                <br />
                                {{ $t('BacktoList') }}
                            </RouterLink>
                        </span>
                        <span class="collapse-head-btns">
                            <a href="javascript:;" class="collapse-btns">
                                <em class="fa fa-angle-up text-white"></em>
                            </a>
                        </span>
                    </div>
                </div>
                <div id="WorktypeData" class="col-md-12 p-0" v-if="isDetail">
                    <div class="card mb-2 probx">
                        <div class="card-header">
                            <h2>{{ $t('Details') }}</h2>
                        </div>
                        <div class="card-body">
                            <div class="row" v-for="item in WorktypeSourceList">
                                <div class="col-md-6 col-lg-4">
                                    <div class="form-group">
                                        <label><b>{{ $t('WorkTypeName') }}:</b> {{ item.worktype_name }}</label>
                                    </div>
                                </div>
                                <div class="col-md-6 col-lg-4">
                                    <div class="form-group">
                                        <label v-if="item.duration_type == 'HH'"><b>{{ $t('DurationType') }}:</b>
                                            {{ $t('Hours') }}</label>
                                        <label v-if="item.duration_type == 'MM'"><b>{{ $t('DurationType') }}:</b>
                                            {{ $t('Minutes') }}</label>
                                    </div>
                                </div>
                                <div class="col-md-6 col-lg-4">
                                    <div class="form-group">
                                        <label><b>{{ $t('AutoCreateService') }}:</b> {{ item.auto_create_sa == 1 ? 'Yes' :
                                            'No' }}</label>
                                    </div>
                                </div>
                                <div class="col-md-6 col-lg-4">
                                    <div class="form-group">
                                        <label><b>{{ $t('MinimumCrewSize') }}:</b> {{ item.min_crew_size }}</label>
                                    </div>
                                </div>
                                <div class="col-md-6 col-lg-4">
                                    <div class="form-group">
                                        <label><b>{{ $t('RecommendedCrewSize') }}:</b> {{ item.rec_crew_size }}</label>
                                    </div>
                                </div>
                                <div class="col-md-6 col-lg-4">
                                    <div class="form-group">
                                        <label><b>{{ $t('EstimateDuration') }}:</b> {{ item.estimate_duration }}</label>
                                    </div>
                                </div>
                                <div class="col-md-6 col-lg-4">
                                    <div class="form-group">
                                        <label><b>{{ $t('MaxDuration') }}:</b> {{ item.maximum_duration }}</label>
                                    </div>
                                </div>
                                <div class="col-md-6 col-lg-4">
                                    <div class="form-group">
                                        <label><b>Color:</b> <span
                                                :style="'background-color: ' + item.color + ';display: inline-block;width: 60px;border-radius: 10px;height: 17px;'">&nbsp;</span></label>
                                    </div>
                                </div>

                                <div class="col-lg-12">
                                    <div class="form-group">
                                        <label><b>{{ $t('description') }}:</b> {{ item.description }}</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 2 }"
                    id="tab-service-content" role="tabpanel" aria-labelledby="tab-service-content" v-if="isService">
                    <WorktypeSADataModal></WorktypeSADataModal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 3 }"
                    id="tab-WOStatus-content" role="tabpanel" aria-labelledby="tab-WOStatus-content" v-if="isWOStatus">
                    <WOStatusMappingModal :CompanyType="CompanyType"></WOStatusMappingModal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 4 }"
                    id="tab-Skill-content" role="tabpanel" aria-labelledby="tab-Skill-content" v-if="isSkill">
                    <SkillMappingModal></SkillMappingModal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 5 }"
                    id="tab-Product-content" role="tabpanel" aria-labelledby="tab-Product-content" v-if="isProduct">
                    <ProductMappingModal></ProductMappingModal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 6 }"
                    id="tab-CDCCode-content" role="tabpanel" aria-labelledby="tab-CDCCode-content" v-if="isCDCMap">
                    <CDCCodeWorkTypeModal></CDCCodeWorkTypeModal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 7 }"
                    id="tab-FormMapping-content" role="tabpanel" aria-labelledby="tab-FormMapping-content" v-if="isFormMap">
                    <FormMappingModal></FormMappingModal>
                </div>
            </div>
        </section>
    </div> 
</template>
<script>
import FormMappingModal from '../../views/WorkType/DetailsComponent/FormMapping.vue'
import WOStatusMappingModal from '../../views/WorkType/DetailsComponent/WOStatusMapping.vue'
import WorktypeSADataModal from '../../views/WorkType/DetailsComponent/WorktypeSAData.vue'
import SkillMappingModal from '../../views/WorkType/DetailsComponent/SkillMapping.vue'
import ProductMappingModal from '../../views/WorkType/DetailsComponent/ProductMapping.vue'
import CDCCodeWorkTypeModal from '../../views/WorkType/DetailsComponent/CDCCodeWorkType.vue'
import DataServices from "@/services/DataService";
export default {
    components: { WOStatusMappingModal, WorktypeSADataModal, SkillMappingModal, ProductMappingModal, CDCCodeWorkTypeModal, FormMappingModal },
    data() {
        return {
            
            WorktypeSourceList: [],
            isDetail: false,
            isService: false,
            NoRecord: false,
            isSkill: false,
            isWOStatus: false,
            isFormMap:false,
            isProduct: false,
            isCDCMap: false,
            WOStatusMappingModal: false,
            SkillMappingModal: false,
            ProductMappingModal: false,
            CDCCodeWorkTypeModal:false,
            FormMappingModal:false,
            WorktypeSADataModal: false,
            tabNameTitle: '',
            CompanyType:'',
            user_id: null,
            companyId: null,
            user_type: '',
            created_by: '',
        }
    },
    created: function () {
        var vm = this;
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        this.IsExtraMapFeature();
        this.loadData("detail");
        this.FetchData();
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            var url = `Id=${this.$route.params.id}`;
            DataServices.FsmGetWorktypeDetailById(url).then(
                function (response) {
                    vueObj.WorktypeSourceList = [];
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            vueObj.WorktypeSourceList = response.data.result.data;
                            vueObj.Worktype_name = vueObj.WorktypeSourceList[0].worktype_name;
                            vueObj.created_by = vueObj.WorktypeSourceList[0].created_by;
                            if (vueObj.WorktypeSourceList.length > 0) {
                                vueObj.NoRecord = false;
                            }
                            else {
                                vueObj.NoRecord = true;
                            }
                        }
                        else {
                            vueObj.NoRecord = true;
                        }
                    }
                });
        },
        loadData: function (tabName, callFrom) {
            var vm = this;
            if (tabName == "detail") {
                vm.isService = false;
                vm.isSkill = false;
                vm.isWOStatus = false;
                vm.isProduct = false;
                vm.isCDCMap = false;
                vm.isFormMap=false;
                vm.isDetail = true;
                vm.tabNameTitle = 'WorkType Overview';
                vm.activeKey = 1;
            } else if (tabName == "Service") {
                vm.isSkill = false;
                vm.isWOStatus = false;
                vm.isProduct = false;
                vm.isCDCMap = false;
                vm.isDetail = false;
                vm.isFormMap=false;
                vm.isService = true;
                vm.tabNameTitle = 'Service Appointment Details';
                vm.activeKey = 2;
                vm.openWTServiceAppointmentDetail(this.$route.params.id);
            } else if (tabName == "WoStatus") {
                vm.isService = false;
                vm.isSkill = false;
                vm.isProduct = false;
                vm.isCDCMap = false;
                vm.isDetail = false;
                vm.isFormMap=false;
                vm.isWOStatus = true;
                vm.tabNameTitle = 'Status Code Mapping List';
                vm.activeKey = 3;
                vm.openPopupForWOStatusMapping(this.$route.params.id);
            } else if (tabName == "Skill") {
                vm.isService = false;
                vm.isWOStatus = false;
                vm.isProduct = false;
                vm.isCDCMap = false;
                vm.isDetail = false;
                vm.isFormMap=false;
                vm.isSkill = true;
                vm.tabNameTitle = 'Skill Mapping Listing';
                vm.activeKey = 4;
                vm.openPopupForSkillMapping(this.$route.params.id);
            } else if (tabName == "Product") {
                vm.isService = false;
                vm.isSkill = false;
                vm.isWOStatus = false;
                vm.isCDCMap = false;
                vm.isDetail = false;
                vm.isFormMap=false;
                vm.isProduct = true;
                vm.tabNameTitle = 'Product Mapping Listing';
                vm.activeKey = 5;
                vm.openPopupForProductMapping(this.$route.params.id);
            } else if (tabName == "CDCCode") {
                vm.isService = false;
                vm.isSkill = false;
                vm.isWOStatus = false;
                vm.isProduct = false;
                vm.isDetail = false;
                vm.isFormMap=false;
                vm.isCDCMap = true;
                vm.tabNameTitle = 'CDC Code Mapping List';
                vm.activeKey = 6;
                vm.openPopupForCDCCode(this.$route.params.id);
            }else if (tabName == "FormMapping") {
                vm.isService = false;
                vm.isSkill = false;
                vm.isWOStatus = false;
                vm.isProduct = false;
                vm.isDetail = false;
                vm.isCDCMap = false;
                vm.isFormMap=true;
                vm.tabNameTitle = 'Form Mapping';
                vm.activeKey = 7;
                vm.openPopupForFormMapping(this.$route.params.id);
            }
        },
        IsExtraMapFeature: function () {
            var vueObj = this;
            DataServices.IsExtraMapFeature().then(
                function (response) {
                    vueObj.CompanyType = "";
                    if (response.data != null && response.data != "") {
                       vueObj.CompanyType = response.data;
                    }else{
                        vueObj.CompanyType ='normal';
                    }
                });
        },
        openWTServiceAppointmentDetail: function (id) {
            this.WorktypeSADataModal = !this.WorktypeSADataModal;
        },
        openPopupForWOStatusMapping: function (id) {
            this.WOStatusMappingModal = !this.WOStatusMappingModal;
        },
        openPopupForSkillMapping: function (id) {
            this.SkillMappingModal = !this.SkillMappingModal;
        },
        openPopupForProductMapping: function (id) {
            this.ProductMappingModal = !this.ProductMappingModal;
        },
        openPopupForCDCCode: function (id) {
            this.CDCCodeWorkTypeModal = !this.CDCCodeWorkTypeModal;
        },
        openPopupForFormMapping: function (id) {
            this.FormMappingModal = !this.FormMappingModal;
        },
    },
}
</script>